import * as React from 'react';
import { Link, graphql } from 'gatsby';

import Layout from '../components/layout';
import Seo from '../components/seo';

export default ({ data, location }) => {
  const siteTitle = data.site.siteMetadata?.title || 'Title';
  const posts = data.allMarkdownRemark.nodes || [];
  const pageTitle = 'News';

  return (
    <div>
      <Seo title={pageTitle} />

      <div className="mt-5 pb-5">
        <div className="container">
          <div className="row">
            <div className="col-12">
              <h3>{pageTitle}</h3>
            </div>

            {posts.map(post => {
              const title = post.frontmatter.title || post.fields.slug;
              const slug = post.fields.slug;
              const description = post.frontmatter.description || `${post.excerpt}`;
              const thumbnailImageUrl = post.frontmatter.thumbnailImageUrl;

              return (
                <div className="col-12 col-md-4  mt-4" key={slug}>
                  <Link to={slug} className="text-dark text-decoration-none">
                    <div className="card">
                      <img className="card-img-top" src={thumbnailImageUrl} alt={description} />
                      <div className="card-body">
                        <h5 className="card-title">{title}</h5>
                        <p
                          itemProp="description"
                          className="small"
                          dangerouslySetInnerHTML={{
                            __html: description,
                          }}
                        />
                      </div>
                    </div>
                  </Link>
                </div>
              );
            })}
          </div>
        </div>
      </div>
    </div>
  )
}

export const pageQuery = graphql`
  query {
    site {
      siteMetadata {
        title
      }
    }
    allMarkdownRemark(
      filter: {
        frontmatter: {
          templateKey: {
            eq: "blog-post"
          }
        }
      },
      sort: {
        fields: [frontmatter___date],
        order: DESC
      })
    {
      nodes {
        excerpt(pruneLength: 110)
        fields {
          slug
        }
        frontmatter {
          date(formatString: "MMMM DD, YYYY")
          title
          description
          thumbnailImageUrl
        }
      }
    }
  }`;
